import React from "react";
import "./index.less";
import { FormComponentProps } from "antd/lib/form/Form";
import deleteImage from "../../../assets/image/img_delete_company.png";
import { Button, Modal, Input, Form } from "antd";

interface IFormComponentProps extends FormComponentProps {
  smsCode: string // 短信验证码
  mobile: string // 手机号码
}
class DissolveEnterprise extends React.Component<IFormComponentProps, {
    showVisibleIndex: number;
  }> {
  constructor(props: IFormComponentProps) {
    super(props);
    this.state = {
      showVisibleIndex: 0
    }
  }
  public dissolve = () => {
    this.setState({
      showVisibleIndex: 3,
    })
  }
  public modalCancel = () => {
    this.setState({
      showVisibleIndex: 0,
    })
  }
  public render() {
    const formItemLayout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 16 },
    };
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="mf-setting-dissolve">
        <div className="mf-setting-dissolve__title">解散企业</div>
        <div className="mf-setting-dissolve__content">
          <div className="mf-setting-dissolve__content__logo">
            <img src={deleteImage} alt=""/>
          </div>
          <div className="mf-setting-dissolve__content__desc">
            <p>
              解散企业后<br/>
              1、后台管理账号和通讯录将会删除，请先 <a>导出通讯录</a><br/>
              2、应用数据（视频监控、商务直播等）将会删除，请做好相应的备份<br/>
              3、操作不可取消，请谨慎操作<br/>
            </p>
          </div>
          <div className="mf-setting-dissolve__content__footer">
            <Button className="mf-setting__button" type="danger" onClick={this.dissolve}>解散企业</Button>
          </div>
        </div>
        <Modal
          className="mf-dialog mf-setting-modal"
          centered
          footer={null}
          visible={this.state.showVisibleIndex === 1}
          onOk={this.modalCancel}
          onCancel={this.modalCancel}
        >
          <div className="mf-setting-dissolve__header">
            解散企业
          </div>
          <div className="mf-setting-dissolve__content">
            请先删除企业应用下的所有设备，再解散企业
          </div>
          <div className="mf-setting-dissolve__footer">
            <Button className="mf-setting__button background658FFF" type="primary" onClick={this.modalCancel}>我知道了</Button>
          </div>
        </Modal>
        <Modal
          className="mf-dialog mf-setting-modal"
          centered
          footer={null}
          onCancel={this.modalCancel}
          visible={this.state.showVisibleIndex === 2}
        >
          <div className="mf-setting-dissolve__header">
            解散企业
          </div>
          <div className="mf-setting-dissolve__content">
            请先删除企业所有成员，再解散企业
          </div>
          <div className="mf-setting-dissolve__footer">
            <Button className="mf-setting__button background658FFF" type="primary" onClick={this.modalCancel}>我知道了</Button>
          </div>
        </Modal>
        <Modal
          className="mf-dialog mf-setting-modal"
          centered
          footer={null}
          onCancel={this.modalCancel}
          visible={this.state.showVisibleIndex === 3}
        >
          <div className="mf-setting-dissolve__sort__header">
            <div className="mf-setting-dissolve__sort__header__title">短信验证码</div>
            <div className="mf-setting-dissolve__sort__header__desc">为了确保信息安全，请输入短信验证码</div>
          </div>
          <div className="mf-setting-dissolve__sort__content">
            <Form {...formItemLayout} className="mf-setting-dissolve__sort__form">
              <Form.Item label="手机号码">
                {getFieldDecorator('mobile', {
                  initialValue: "18888888888"
                })(
                  <Input
                    disabled
                    placeholder="mobile"
                    size="large"
                  />,
                )}
              </Form.Item>
              <Form.Item label="验证码">
                {getFieldDecorator('smsCode', {
                  rules: [{ required: true, message: 'Please input your Password!' }],
                })(
                  <div><Input
                  size="large"
                    className="mf-setting-dissolve__sort__content__input"
                    placeholder="请输入短信验证码"
                  /><Button className="mf-setting-dissolve__sort__content__button">发送验证码</Button></div>,
                )}
              </Form.Item>
            </Form>
          </div>
          <div className="mf-setting-dissolve_sort_footer">
            <Button className="mf-setting__button background658FFF" type="primary" onClick={this.modalCancel}>确定</Button>
          </div>
        </Modal>
      </div>
    )
  }

}

export default Form.create()(DissolveEnterprise);
// export default DissolveEnterprise;
